@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main{display:flex;align-items:center;justify-content:center;height:100vh;width:100%;background:#22c1c3;background:linear-gradient(120deg, #22c1c3 0%, #167c7e 47%);color:#fff;font-family:"Roboto",sans-serif;font-size:16px}.main .timer{max-width:550px;justify-content:center}.main .timer button{margin:0 10px;padding:5px 15px;border:2px solid #fff;border-radius:5px;background-color:#404040;color:#fff;box-shadow:rgba(0,0,0,.24) 0px 3px 8px}.main .timer button:hover{background-color:#595959}.main .timer .title{font-size:1.5rem;padding:1rem}.main .timer .display-and-control-timer{border-radius:10px;padding:2rem 0rem;border:4px solid #fff;background-color:#404040;box-shadow:rgba(0,0,0,.25) 0px 25px 50px -12px;margin:1rem 0}.main .timer .display-and-control-timer #timer-label{font-size:1.5rem;margin-bottom:0}.main .timer .display-and-control-timer #time-left{font-weight:bold;color:#ee2b55;font-size:6.5rem;letter-spacing:10px}@media screen and (max-width: 400px){.main .timer .display-and-control-timer #time-left{font-size:4.5rem}}.main .timer .display-and-control-timer .control-timer-btn{color:#fff;background-color:#167c7e;padding:5px 20px;margin:0 5px}.main .timer .display-and-control-timer .control-timer-btn:hover{background-color:#fff;color:#167c7e}.main .timer .config-timer{padding:1rem}.main .timer .config-timer .col{text-align:center}.main .timer .config-timer label{font-size:1.5rem}.main .timer .author{margin-top:3rem;letter-spacing:1px}
